.progress-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 8px;
    background: rgba(255, 255, 255, 0.2);
    z-index: 1000;
  }
  
  .progress-bar {
    height: 100%;
    width: 0%;
    background: #ffffff;
    transition: width 0.5s ease;
  }
  